<template>
  <section id="pano" ref="pano"></section>
  <MarkerContainer v-for="marker in renderedMarkers" :key="marker.id" :node="marker"/>
</template>

<script>
import MarkerContainer from "@/components/MarkerContainer";

let initiated = false;

/* eslint-disable no-undef */
export default {
  name: "StreetView",
  components: {MarkerContainer},
  props: {
    map: Object,
    latLng: Object,
    markers: [Object]
  },
  emits: ['marker-changed'],
  data() {
    return {
      pano: null,
      gMapsMarkers: [],
      renderedMarkers: [],
    }
  },
  mounted() {
    this.pano = new google.maps.StreetViewPanorama(this.$refs["pano"], {
      position: { lat: this.latLng.lat, lng: this.latLng.lng },
      pov: {
        heading: 0,
        pitch: 0,
      },
      addressControlOptions: {
        position: google.maps.ControlPosition.LEFT_BOTTOM
      },
      linksControl: true,
      panControl: true,
      enableCloseButton: false,
      motionTracking: false,
      scrollwheel: false,
      zoomControl: false,
      showRoadLabels: false,
      addressControl: false
    });

    this.map.$mapPromise.then((mapObject) => {
      mapObject.setStreetView(this.pano);

      this.pano.addListener('position_changed', () => {
        const newPos = this.pano.getPosition();
        mapObject.setCenter(newPos);

        this.startMutationObserving();
      });

    });

  },
  methods: {
    checkAllMarkers() {
      //console.log("checkAllMarkers");

      let string = '[src*="woodie_3.png"]';  ///[id*='someId']
      const elem = document.querySelectorAll(string);

      //const id_check = [];
      //this.renderedMarkers = [];
      //console.log("CHECK MARKER", elem);

      Array.from(elem).forEach(el => {

        const pathName = el.getAttribute('src').split('#');
        console.log("ADD THREE JS LAYER", pathName[1]);

        //id_check.push(pathName[1]);
        //this.renderedMarkers.push(divNode)

        if(el.parentNode.childElementCount==1) {

          const divNode = document.createElement('div');

          const markerNode = el.parentNode;
          markerNode.append(divNode);

          //const threeOverlayInstance = defineComponent({
          //  extends: ThreeOverlay
          //})
          //const comp = createApp(threeOverlayInstance)
          //comp.mount(divNode)

          //console.log(divNode)
          this.renderedMarkers.push(divNode)
        }
      });
      //console.log(id_check);
    },
    hideAllMarker() {
      for (let i = 0; i < this.gMapsMarkers.length; i++) {
        this.gMapsMarkers[i].setPosition(null);
        this.gMapsMarkers[i].setMap(null);
      }
      this.gMapsMarkers = [];
    },
    startMutationObserving() {
      const target = document.querySelectorAll('[aria-roledescription="map"]');

      if (!initiated) {
        console.log("TARGET");
        console.log(target);

        const observer = new MutationObserver(list => {
          console.log("observation test");
          console.log(list);

          for (const mutation of list) {
            if(mutation.type == "attributes") {
              //console.log("ATTRIBUTE CHANGED");
              //console.log(mutation);

              this.checkAllMarkers();

            } else if(mutation.type == "childList") {
              //console.log("IMAGE ADDED");
              this.checkAllMarkers();

            }
          }

        });
        observer.observe(target[1], {attributes: true, attributeFilter: ['src'], childList: true, subtree: true});

        initiated = true;
      }
    }
  },
  watch: {
    latLng: {
      handler: function (newValue) {
        this.pano.setPosition(newValue)

      },
      deep: true
    },
    markers: {
      handler: function () {

        this.renderedMarkers = [];

        this.hideAllMarker();

        for (let i = 0; i < this.markers.length; i++) {
          // Create the icon
          const icon = new google.maps.MarkerImage("woodie_3.png",
              new google.maps.Size(300, 200),
              new google.maps.Point(0,0),
              new google.maps.Point(48, 32)
          );
          // Determine a unique id somehow, perhaps from your data
          //const markerId = Math.floor(Math.random() * 1000000);
          icon.url += "#" + i;

          const marker = new google.maps.Marker({
            position: this.markers[i].position,
            map: this.pano,
            draggable: false,
            icon: icon
          });

          google.maps.event.addListener(marker, 'dragend', () => {
            const id = marker.icon.url.split('#')[1];
            const newPosition = marker.position //{ lat: marker.position.lat, lng: marker.position.lng };
            this.$emit('marker-changed', newPosition, id);
            //this.markers[pathName].position = newPosition;
          });

          this.gMapsMarkers.push(marker);
        }
      },
      deep: true
    }
  }
}
</script>

<style scoped>

#pano {
  float: left;
  height: 100%;
  width: 100%;
  background-color: #ff5a5f;
}

</style>