<template>
  <div id="map_wrapper">
    <section :style="mapStyle" id="map-container" ref="map">
      <GMapMap
        ref="mapRef"
        :center=latLng
        :zoom="user ? 16 : 20"
        map-type-id="terrain"
        @click="addMarker"
      >
        <GMapMarker
          :key="index"
          v-for="(m, index) in markers"
          :position="m.position"
          :clickable="true"
          :draggable="true"
          @click="clickThat(m)"
          @dragend="m.position=$event.latLng"
        />
      </GMapMap>
    </section>
    <section :style="panoStyle" id="pano-container">
      <StreetView
        @marker-changed="markerChangedEvent"
        v-if="isLoaded"
        :latLng="latLng"
        :map="mapRef"
        :markers="markers"
      />
    </section>
  </div>
</template>
<script>
import StreetView from "@/components/StreetView";
export default {
  name: 'Map-Component',
  components: {StreetView},
  props: {
    latLng: Object,
    user: Boolean
  },
  computed: {
    mapStyle() {
      return { "width": this.user ? "15%" : "50%" };
    },
    panoStyle() {
      return { "width": this.user ? "85%" : "50%" };
    },
  },
  data() {
    return {
      markers: [
        {
          position: {
            lat: -33.8985415, lng: 151.169633 //-33.8985415,151.169633 //-33.8857607, lng: 151.1894022
          },
        }
      ],
      mapRef: null,
      isLoaded: false
    }
  },
  mounted() {

    this.$refs.mapRef.$mapPromise.then((mapObject) => {

      this.isLoaded = true
      this.mapRef = this.$refs.mapRef

      mapObject.addListener("center_changed", () => {
        // 3 seconds after the center of the map has changed, pan back to the
        // marker.
      });
    });
  },
  methods: {
    addMarker(event) {
      const marker = {
        lat: event.latLng.lat(),
        lng: event.latLng.lng(),
      }
      this.markers.push({ position: marker })
    },
    clickThat: function (m) {
      console.log(m)
    },
    markerChangedEvent(position, id) {
      this.markers[id].position = position
    }
  }
}
</script>

<style>

  body {
    height: 100%;
    margin: 0;
    padding: 0;
  }

  #map_wrapper{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
  }

  #map-container, #pano-container {
    float: left;
    height: 100%;
  }
</style>