<template>
  <div>
    <section class="ui two column centered grid" style="position:relative;z-index:10">
      <div class="column">
        <div class="ui message red" v-show="error">{{error}}</div>
        <div class="ui segment large form" style="background: #1b1b1ec2;">
          <div class="field">
            <div class="ui right icon input large" :class="{loading:spinner}">
              <GMapAutocomplete
                  placeholder="This is a placeholder"
                  @place_changed="setPlace"
              >
              </GMapAutocomplete>
              <i class="dot circle link icon" @click="locatorButtonPressed"></i>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>
<script>
import axios from "axios";
import { store } from "../main.js";

export default {
  data() {
    return {
      address: "",
      error: "",
      spinner: false,
    };
  },
  methods: {
    setPlace(place) {
      this.$emit('location-changed', place.geometry.location.lat(), place.geometry.location.lng());
    },
    locatorButtonPressed() {
      this.spinner = true;

      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
            (position) => {

              this.getAddressFrom(
                  position.coords.latitude,
                  position.coords.longitude
              );
              this.$emit('location-changed', position.coords.latitude, position.coords.longitude);
            },
            (error) => {
              this.error =
                  "Locator is unable to find your address. Please type your address manually.";
              this.spinner = false;
              console.log(error.message);
            }
        );
      } else {
        this.error = error.message;
        this.spinner = false;
        console.log("Your browser does not support geolocation API ");
      }
    },
    getAddressFrom(lat, long) {
      axios
          .get(
              "https://maps.googleapis.com/maps/api/geocode/json?latlng=" +
              lat +
              "," +
              long +
              "&key=" +
              this.$store.state.apiKey
          )
          .then((response) => {
            if (response.data.error_message) {
              this.error = response.data.error_message;
              console.log(response.data.error_message);
            } else {
              this.address = response.data.results[0].formatted_address;
            }
            this.spinner = false;
          })
          .catch((error) => {
            this.error = error.message;
            this.spinner = false;
            console.log(error.message);
          });
    },
  },
  emits: ['location-changed', 'marker-pressed', 'sizeChanged-pressed'],
};
</script>


